html { font-size: 16px; }

body { min-height: 100vh; background: #fff; color: #333; font-family: 'Helvetica Neue', Helvetica, sans-serif; line-height: 1.5; }

a { color: #e5432a; cursor: pointer; transition: all 0.3s; }

a:hover { color: #2aa3e5; }

img { display: inline-block; height: auto; max-width: 100%; }

p { color: inherit; font-family: inherit; font-size: 1em; font-weight: inherit; font-style: inherit; line-height: inherit; }

p, ul, ol { margin: 1.25em 0; }

h1, h2, h3, h4, h5, h6 { font-weight: 700; line-height: 1.25; margin: 0.5em 0; }

h1 { font-size: 3em; text-align: center; }

h2 { font-size: 2.66667em; }

h3 { font-size: 2em; }

h4 { font-size: 1.33333em; }

h5 { font-size: 1em; }

h6 { font-size: 0.8em; }

@media (min-width: 768px), print { h1 { font-size: 4.5em; }
  h2 { font-size: 4em; }
  h3 { font-size: 3em; }
  h4 { font-size: 2em; }
  h5 { font-size: 1.5em; }
  h6 { font-size: 1.2em; } }

@media (min-width: 1201px), print { h1 { font-size: 6em; } }

.location { position: relative; background: #ebebeb; margin-bottom: 2rem; padding: 2rem; }

.location .title { font-weight: bold; margin: 0; }

.location .message { font-weight: bold; color: #2aa3e5; }

.location-collection > ul { margin: 0; padding: 0; list-style-type: none; }

.location-collection > ul > li { position: relative; }

.location-collection .delete-location { position: absolute; top: 0.5rem; right: 0.5rem; padding: 0.25rem 0.5rem; border-radius: 4px; background: #ccc; color: #e52a2a; font-weight: 700; transition: all 0.35s; cursor: pointer; z-index: 1; }

.location-collection .delete-location:hover { background-color: #e52a2a; color: #fff; }

.location-collection .new-location-form { display: flex; position: relative; margin-bottom: 2rem; }

.location-collection .new-location-form input[type="text"] { all: unset; flex: 1; background: #ebebeb; padding: 0.5rem 1rem; cursor: text; color: #555; }

.location-collection .new-location-form input[type="submit"] { width: 0.1px; height: 0.1px; opacity: 0; overflow: hidden; position: absolute; z-index: -1; }

.location-collection .new-location-form .overlay { display: none; position: absolute; left: 0; right: 0; top: 0; bottom: 0; background-color: #ebebeb; padding: 0.5rem 1rem; color: #8e8e8e; cursor: default; }

.location-collection .new-location-form.loading .overlay { display: block; }

#alert-area { position: fixed; left: 0; right: 0; top: 0; }

#alert-area .alert { display: none; background: #e52a2a; color: #fff; font-weight: 700; font-size: 1.4em; padding: 1rem 2rem; cursor: pointer; }

.row { position: relative; overflow: hidden; max-width: 1201px; margin: auto; }

.row > .column { border-left: 1rem solid transparent; border-right: 1rem solid transparent; }

@media (min-width: 768px), print { .row > .column { border-left-width: 1.5rem; border-right-width: 1.5rem; width: 50%; float: left; } }

.button { background: #2aa3e5; color: #fff; padding: 0.5rem 1rem; cursor: pointer; transition: background-color 0.35s; }

.button:hover { background: #1781bb; }
